.app {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: var(--primary-color);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.buttons {
  width: 100%;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.rotate {
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  width: 160px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: var(--secondary-color);
  border-radius: 10px;
}

.rotate:hover, .flip:hover {
  -webkit-animation: blacken 0.25s forwards;
          animation: blacken 0.25s forwards;
}

@-webkit-keyframes blacken {
  to {
    background-color: black;
    color: white;
  }
}

@keyframes blacken {
  to {
    background-color: black;
    color: white;
  }
}

.flip {
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  width: 160px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: var(--secondary-color);
  border-radius: 10px;
}


