.board {
    width: 100%;
    background-color: var(--primary-color);
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
  
    display: grid;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    -ms-flex-pack: center;
        justify-content: center;
    grid-template-columns: repeat(var(--board-num-rows), var(--board-cell-size));
    grid-template-rows: repeat(var(--board-num-cols), var(--board-cell-size));
    grid-gap: var(--board-gap);
}


