.arsenal {
    background-color: var(--secondary-color);
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    
    display: grid;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 5px;
    grid-template-columns: repeat(var(--arsenal-num-cols), var(--arsenal-cell-size));
    grid-template-rows: repeat(var(--arsenal-num-rows), var(--arsenal-cell-size));
    grid-gap: var(--arsenal-gap);
}


